import {useState, useEffect} from 'react';
import {generateFromConfig} from 'multi-layers-image';

const lib = [
  {name: 'background', qty: 10},
  {name: 'neck', qty: 3},
  {name: 'body', qty: 16},
  {name: 'ears', qty: 9},
  {name: 'face', qty: 3},
  {name: 'eyebrows', qty: 18},
  {name: 'hair', qty: 30},
  {name: 'beard', qty: 30},
  {name: 'mouth', qty: 3},
  {name: 'eyes', qty: 4},
  {name: 'nose', qty: 9},
  {name: 'glasses', qty: 10},
  {name: 'piercing', qty: 6},
];

const prepareConfig = () =>
  lib.map(({name, qty}) => ({
    name,
    images: Array.from(Array(qty), (x, index) =>
      name === 'layout'
        ? `/assets/svg/${name}/F${index + 1}.svg`
        : `/assets/svg/${name}/${index}.svg`
    ),
  }));

const useLogic = initialState => {
  const [avatar, setAvatar] = useState(undefined);
  const [seed, setSeed] = useState(
    (Math.random() + 1).toString(36).substring(2)
  );

  useEffect(() => {
    (async () => {
      setAvatar(
        await generateFromConfig(seed, prepareConfig(), {
          width: 250,
          height: 250,
          mimetype: 'image/svg+xml',
        })
      );
    })();
  }, [seed]);

  return {
    avatar,
    seed,
    setSeed,
  };
};

export default useLogic;
